.required:after{
    content: " *";
    font-size: 14px;
    color: #e3342f;
}


.img-thumbnail {
    width: 250px;
    height: 150px;
}

@media (max-width: 767px) {
    .table-responsive .dropdown-menu {
        position: static !important;
    }
    .alert {
        width: 90%;
    }
}
@media (min-width: 768px) {
    .table-responsive {
        overflow: visible;
    }
    .alert {
        width: 50%;
    }
}
// table scrollable yaxis
.table-responsive {
overflow-y:scroll;
}

//alert width
.alert {
    // width: 50%;
    z-index: 1;
    position: fixed;
    right: 25px;
    bottom: 5px;
}

